"use client"
import { useState, useEffect } from "react";
import Link from "next/link";


const links = [
  { href: "/", label: "Home" },
  { href: "/menu", label: "Menu" },
  { href: "/about", label: "About" },
  { href: "/contact", label: "Contact" }
];
export default function Navbar() {
        const [scrolled, setScrolled] = useState(false);
      
        useEffect(() => {
          const handleScroll = () => {
            setScrolled(window.scrollY > 50); // shrink after 50px
          };
          window.addEventListener("scroll", handleScroll);
          return () => window.removeEventListener("scroll", handleScroll);
        }, []);
      
        return (
          <header
            className={`fixed top-0 inset-x-0 z-50 backdrop-blur border-b transition-all duration-300 ${
              scrolled ? "bg-white/95 h-14 shadow-md" : "bg-white/80 h-20"
            }`}
          >
            <div className="container-page flex items-center justify-between h-full">
              <Link href="/" className="flex items-center gap-2">
                <img
                  src="/logo.png"
                  alt="The Charging Ground"
                  className={`w-auto transition-all duration-300 ${
                    scrolled ? "h-8" : "h-16 md:h-18"
                  }`}
                />
              </Link>
      
              <nav className="flex items-center gap-1">
                {links.map((l) => (
                  <Link
                    key={l.href}
                    href={l.href}
                    className="px-3 py-2 rounded-xl text-sm hover:bg-neutral-100"
                  >
                    {l.label}
                  </Link>
                ))}
                <Link href="/menu" className="ml-2 btn btn-primary">
                  Order Soon
                </Link>
              </nav>
            </div>
          </header>
        );
      }